/* app.css */

/* Global styles */
body {
  font-family: 'Roboto', sans-serif;
  background-color: #f0f2f5;
}

/* Header styles */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #001529;
  padding: 0 24px;
}

.header .logo {
  margin-right: 20px;
}

.menu {
  line-height: 64px;
}

.menuItem {
  margin-left: auto;
}

/* Content styles */
.content {
  padding: 50px;
}

.contentInner {
  max-width: 800px;
  margin: 0 auto;
  padding: 24px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

/* Footer styles */
.footer {
  text-align: center;
  background-color: #001529;
  color: #fff;
  padding: 16px;
}
